import axios from 'axios'

axios.defaults.headers.post['Content-Type'] = 'application/json'

//  axios 
const service = axios.create({
  timeout: 30000
})

// 
service.interceptors.request.use(
  async config => {
      config.data = config.data || {}
      config.baseURL = process.env.VUE_APP_baseURL
      console.log(config.baseURL)
      return config
    },
    error => {
      // 
      // console.log(error)
      Promise.reject(error)
    }
)

// 
service.interceptors.response.use(
  response => {
    const data = response.data
    return data
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '';
          break
        case 401:
          error.message = '';
          break
        case 403:
          error.message = '';
          break
        case 404:
          error.message = `: ${error.response.config.url}`;
          break
        case 408:
          error.message = '';
          break
        case 500:
          error.message = '';
          break
        case 501:
          error.message = '';
          break
        case 502:
          error.message = '';
          break
        case 503:
          error.message = '';
          break
        case 504:
          error.message = '';
          break
        case 505:
          error.message = 'HTTP';
          break
        default:
          break
      }
    }
    // log.error(error)
    return Promise.reject(error)
  }
)

export default service