<template>
  <div class="address">
    <van-nav-bar title=""
                 left-arrow
                 @click-left="onClickLeft" />
    <van-address-edit :area-list="areaList"
                      button-type="info"
                      show-postal
                      show-delete
                      show-set-default
                      show-search-result
                      :search-result="searchResult"
                      :area-columns-placeholder="['', '', '']"
                      @save="onSave"
                      @delete="onDelete"
                      @change-detail="onChangeDetail" />
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data () {
    return {
      areaList: {
        province_list: {
          110000: '',
          120000: ''
        },
        city_list: {
          110100: '',
          110200: '',
          120100: '',
          120200: ''
        },
        county_list: {
          110101: '',
          110102: '',
          110105: '',
          110106: '',
          120101: '',
          120102: '',
          120103: '',
          120104: '',
          120105: '',
        }
      },
      searchResult: [],
    };
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    onSave (content) {
      Toast('save');
      console.log(content)
      this.$router.replace({ path: '/order', query: { content: content.addressDetail } })
    },
    onDelete () {
      Toast('delete');
    },
    onChangeDetail (val) {
      if (val) {
        this.searchResult = [
          {
            name: '',
            address: '',
          },
        ];
      } else {
        this.searchResult = [];
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.address {
  /deep/.van-button--danger {
    background: #1989fa;
    border: 1px solid #1989fa;
  }
}
</style>