<template>
  <div id="app">
    <van-loading v-show="loading"
                 size="24px">...</van-loading>
    <keep-alive>
      <router-view :seller="seller"
                   :ratings="ratings" />
    </keep-alive>
  </div>
</template>

<script>
import { getAll } from '@/api/send'
export default {
  // 
  data () {
    return {
      seller: {
        'name': '',
        'description': '',
        'deliveryTime': 38,
        'score': 4.2,
        'serviceScore': 4.1,
        'foodScore': 4.3,
        'rankRate': 69.2,
        'minPrice': 20,
        'deliveryPrice': 4,
        'ratingCount': 24,
        'sellCount': 90,
        'bulletin': `
          0
          20082013`,
        'avatar': 'http://static.galileo.xiaojukeji.com/static/tms/seller_avatar_256px.jpg'
      },
      ratings: [],
      loading: false
    }
  },
  created () {
    // this.getAll()
  },
  methods: {
    getAll () {
      getAll()
        .then((res) => {
          if (res.code === '200') {
            this.seller = res.data.seller
            this.ratings = res.data.ratings
          } else {
            this.$toast('')
          }
        })
        .catch((err) => {
          console.error(err)
          this.$toast('')
        })
        .finally(() => {
          this.loading = false
        })
    }

  }
}
</script>

<style lang="scss" scoped>
#app {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
</style>
